import './Nav.css';
import Button from '../../utils/Button/Button';
import btnBgLogo from '../../assets/images/btn-bg-logo.webp';
import Timer from './Timer';

const Nav = ({ robots, isGameActive }) => {
  return (
    <nav>
      <Button text="Robot City" backgroundImg={btnBgLogo} />

      <div className="roster-checklist">
        {robots.map((robot) => {
          return (
            <div
              key={robot.id}
              className={`robot-picture-holder ${
                robot.hasBeenFound && 'found-robot'
              }`}
            >
              <img src={robot.picture} alt={robot.name} />
            </div>
          );
        })}
      </div>

      <Timer isGameActive={isGameActive} />
    </nav>
  );
};

export default Nav;
