import './ContextMenu.css';

const ContextMenu = ({ robots, searchCoords, handleContextMenuClick }) => {
  // List of City Robots yet to be found
  let missingRobots = robots
    .filter((robot) => !robot.hasBeenFound)
    .map((robot) => (
      <li
        key={robot.id}
        onClick={(e) => {
          e.stopPropagation();
          handleContextMenuClick(robot, searchCoords);
        }}
      >
        <img src={robot.picture} alt={robot.name} />
        {robot.name}
      </li>
    ));

  return (
    <div
      className="context-menu"
      style={{
        left: `${searchCoords.x}px`,
        top: `${searchCoords.y}px`,
      }}
    >
      {missingRobots}
    </div>
  );
};

export default ContextMenu;
